import { h } from '@stencil/core';
import { newSpecPage } from '@stencil/core/testing';
import { RwcCard } from '../card';

const containerExpectedClassesDefault = ['container', 'shadow', 'border', 'animate-border', 'pointer'];

describe('rwc-card', () => {
  it('should render the card', async () => {
    const page = await newSpecPage({
      components: [RwcCard],
      template: () => (
        <rwc-card
          renderShadow={ true }
          renderBorder={ true }
          animateBorder={ true }
          pointer={ true }
        >
          Card Text
        </rwc-card>
      ),
    });

    expect(page.root).toEqualHtml(`
      <rwc-card
        render-shadow=""
        render-border=""
        animate-border=""
      >
        <mock:shadow-root>
          <div class="${ containerExpectedClassesDefault.join(' ') }">
            <slot></slot>
          </div>
        </mock:shadow-root>
        Card Text
      </rwc-card>
    `);
  });
})
